
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import SelectTab from "components/SelectTab/selectTab";
export default{
components: {
XHeader,Toast,SelectTab
},
data(){
return {
noData: false,
TabDefault1:{key:'0',val:'全部类型'},
TabDefault2:{key:'0',val:'全部'},
select1: [{key:'0',val:'全部类型'},{key:'1',val:'严重精神障碍'},{key:'2',val:'肺结核'},],
select2: [{key:'0',val:'全部'},{key:'1',val:'近三月'},{key:'2',val:'近半年'},{key:'3',val:'近一年'}],
list: [],
isloading: false,
curIndex: 0,
type:"0",//默认全部
time:"0",//默认查3月
}
},
mounted(){
this.initList();
},
methods: {
pullingUp(){
if(this.list.length < 5){
this.isloading = true;
let THIS = this;
var timer = setTimeout(function(){
THIS.initList();
THIS.$refs.scroll.finishPullUp();
},1000)
}else{
this.isloading = false;
this.$vux.toast.text('没有更多了', 'bottom');

this.$refs.scroll.finishPullUp();
}
},
info1(title,info){

if(title == "JSB"){
return ("康复措施 : " + this.getString(info));
}else if(title == "FJH"){
return ("治疗月序 : 第" + this.getString(info) + '月');
}else if(title == "FJHDYS"){
return ("");
}else{
return '';
}
},
info2(type,info){

if(type == "JSB"){
return ("随访分类 : " + this.getString(info));
}else if(type == "FJH" || type == "FJHDYS"){
return ("督导人员 : " + this.getString(info));
}
},
getString(info){
return typeof info=='undefined'?'':info;
},
initList(i){

this.$ajax.post('',{
'MSH.1':'zbsf',
'MSH.2':'zbsfList',
'CHZBM':this.$root.loginInfo.CHZBM,
'pageIndex':this.pageIndex,
'pageSize':5,
'GRDAID':this.$root.loginInfo.grdaid,
"type": this.type,
"time": this.time,
'token':this.$root.loginInfo.token,
}).then(res => {
if(res.rows && res.rows.length > 0){

this.list = this.list.concat(res.rows);

this.pageIndex += 1;

if(this.list.length == 0){
this.noData = true;
}else{
this.noData = false;
}
}else{
if(this.list.length == 0){
this.noData = true;
}else{
this.noData = false;
}
this.isEmpty = true;
}
}).catch(error => {
console.log(error);

});
},
cancerDetail(type,SFJLID,YLJGID,GLKID){
if(type == 'JSB'){
this.$router.push({name:'PsychosisFollowUp',params:{
SFJLID:SFJLID,YLJGID:YLJGID
}});
}else if(type == 'FJH' || type == 'FJHDYS'){
this.$router.push({name:'TBFollowUp',params:{
type:type,SFJLID:SFJLID,YLJGID:YLJGID,GLKID:GLKID
}});
}
},
selChange1(val) {
this.type = val.key;
this.list.length = 0;
this.pageIndex = 1;
this.initList();
},
selChange2(val) {
this.time = val.key;
this.list.length = 0;
this.pageIndex = 1;
this.initList();
},

}
}



